import React from 'react';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import styles from "./ListUsers.module.sass";


const ListItem = ({idItem, first_name, last_name, avatar, price, pay, idVk, getPlatform}) => {
    const urlUser = 'https://vk.com/id' + idVk;
    return (
        <Div className={styles.div}>
            <span className={styles.listNumber}>{idItem}</span>

            {getPlatform === "ios"
                ?
                <Cell
                    className={styles.listName}
                    before={avatar ? <Avatar src={avatar}/> : <Avatar src='https://vk.com/images/camera_200.png'/>}
                    href={idVk === 0 ? '' : urlUser}
                >
                    {first_name ? first_name + " " + last_name : "Место для тебя"}
                </Cell>
                :
                <Cell
                    className={styles.listName}
                    before={avatar ? <Avatar src={avatar}/> : <Avatar src='https://vk.com/images/camera_200.png'/>}
                    description={'Цена: ' + price + ' руб'}
                    href={idVk === 0 ? '' : urlUser}
                >
                    {first_name ? first_name + " " + last_name : "Место для тебя"}
                </Cell>
            }


            {getPlatform === "ios"
                ?
                <Button id={idItem} className={styles.btnDisabled} level="disabled">
                    Занять
                </Button>
                :
                <Button id={idItem} className={styles.button} level="commerce" onClick={pay}>
                    Занять
                </Button>
            }
        </Div>
    );
};

export default ListItem;

