import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import {useNavigatorOnline} from '@oieduardorabelo/use-navigator-online';
import styles from "../styles/custom-styles.sass";

import ListItem from './ListItem';
import ListGroup from './ListGroup';

const ListMain = ({id, go, pay, tabUsers, tabGroups, activeTab, dataUsers, test, dataGroups, getPlatform}) => {
    let {isOnline, isOffline} = useNavigatorOnline();

    const renderUsers = dataUsers.map(item => {
        return (
            <ListItem
                key={item.id}
                idItem={item.id}
                idVk={item.id_vk}
                avatar={item.avatar}
                price={item.price}
                pay={pay}
                first_name={item.first_name}
                last_name={item.last_name}
                test={test}
                getPlatform={getPlatform}
            />
        );
    });

    const renderGroups = dataGroups.map(item => {
        return (
            <ListGroup
                key={item.id}
                idItem={item.id}
                avatar={item.avatar}
                price={item.price}
                go={go}
                name={item.name}
                idVkGroup={item.id_group}
                getPlatform={getPlatform}
            />
        );
    });

    return (
        <Panel id={id}>
            <PanelHeader>
                Рейтинг лидеров
            </PanelHeader>
            <FixedLayout vertical="top">
                <Tabs theme="header" type="buttons">
                    <HorizontalScroll>
                        <TabsItem
                            onClick={tabUsers}
                            selected={activeTab === 'users'}
                        >
                            Люди
                        </TabsItem>
                        <TabsItem
                            onClick={tabGroups}
                            selected={activeTab === 'groups'}
                        >
                            Сообщества
                        </TabsItem>
                    </HorizontalScroll>
                </Tabs>
            </FixedLayout>


            {isOffline
                ?
                <Div className="divSpinner">
                    <ScreenSpinner size='large'/>
                    <Div>
                        Отсутствует интернет - соединение!
                    </Div>
                </Div>
                :
                <>
                    {activeTab === 'users'
                        ?
                        <Group title="default"
                               description="Каждое место покупается на 14 дней.При каждой повторной покупке места,
                       стоимость увеличивается на коэффициент - 1,1 и добавляется + 3 дня к времени пребывания в рейтинге.
                       Выше стоимость места - длительнее время пребывания в рейтинге.">
                            {getPlatform === "ios"
                                ?
                                <Div>
                                    К сожалению, данный сервис не работает на устройствах с системой iOS
                                </Div>
                                :
                                ""
                            }
                            <List>
                                {renderUsers}
                            </List>
                        </Group>
                        :
                        ''
                    }

                    {activeTab === 'groups'
                        ?
                        <Group title="default"
                               description="Каждое место покупается на 14 дней.При каждой повторной покупке места,
                       стоимость увеличивается на коэффициент - 1,1 и добавляется + 3 дня к времени пребывания в рейтинге.
                       Выше стоимость места - длительнее время пребывания в рейтинге.">
                            {getPlatform === "ios"
                                ?
                                <Div>
                                    К сожалению, данный сервис не работает на устройствах с системой iOS
                                </Div>
                                :
                                ""
                            }
                            <List>
                                {renderGroups}
                            </List>
                        </Group>
                        :
                        ''
                    }
                </>
            }
        </Panel>
    );
};
ListMain.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};

export default ListMain;


